.main-menu{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    font-family: 'Righteous', cursive;
    h1{
        color: #3f3f3f;
    }
    .menu{
        display: flex;
        flex-direction: row;
        height: 25%;
        a{
            text-decoration: none;
            .menu_option{
                width: 12rem;
                height: 12rem;
                border: 4px solid whitesmoke;
                padding: 0.5rem;
                margin: 1rem;
                p{
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    background-color: whitesmoke;
                    color: #242424;
                    height: 100%;
                    width: 100%;
                    font-size: 2rem;
                    cursor: pointer;
                    transition: .2s;
    
                    &:hover{
                        background-color: #242424;
                        color: whitesmoke; 
                    }
                }
            }
        }
    }
}