.form4-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    button{
        margin: 2rem;
    }
    .form4{
        display: grid;
        grid-gap: 2rem 1.5rem;
        grid-template-columns: 1fr 5fr;
        grid-template-rows: 5;
        div{
            text-align: left;
        }
        .field-type{
            grid-column-start: 1;
            &:focus{
                border: none;
                box-shadow: none;
            }
        }
        .input_src{
            grid-column-start: 2;
            &:focus{
                border: none;
                box-shadow: none;
            }
        }
    }
    .form2{
        @extend .form4;
        grid-template-rows: 3;
    }
}